<template>
	<div class="main">
		<h2 class="auth-h2">Для совершения заказов на нашем сайте, вам необходимо войти в систему</h2>
		<div class="auth-form">
			<div class="fields">
				<div class="auth-field">
					<label class="form-label">Логин</label>
					<input class="form-control" id="exampleInputEmail1" v-model="email">
				</div>

				<div class="auth-field">
					<label class="form-label">Пароль</label>
					<input class="form-control" id="exampleInputEmail1" v-model="pass" type="password">
				</div>
				<div class="others">
					<button class="btn btn-primary" @click="login">Войти</button>
					<router-link class="link" to="/reg">У меня нет аккаунта</router-link>
				</div>
				
				<div class="auth-field">
					<label class="form-label">Войти с помощью</label>
					<div class="social">
						<button class="ico vk" @click="ShowMSG">
							<img class="imic" src="@/assets/vk.svg">
						</button>
						<button class="ico fb">
							<img class="imich" src="@/assets/fb.svg">
						</button>
						<button class="ico od">
							<img class="imich" src="@/assets/ok.svg">
						</button>
					</div>
				</div>
			</div>
		</div>
	</div>
	
</template>

<script>
export default {
  name: "Autho",
  data() {
    return {
      email: "",
      pass: "",
      version: "1.0",
      address: "http://tpis.std-913.ist.mospolytech.ru",
      user: {}
    }
  },
  components: {},
  methods: {
    ShowMSG(){
      alert(this.email)
    },
    async login() { // По нажатию на кнопку "Войти"
      if (this.email.length > 0 && this.pass.length > 0) {// Проверяем, чтобы поля логина и пароля не были пустыми
        try {
          await this.$http.get(`${this.address}/login/${this.email}/${this.pass}`)
              .then((res) => res.json()).then((res) => (this.user = res));
          console.log("1")
          if (this.user !== -1 && this.user !== null) { // Если вход успешен, записываем пользователя в cookie
            document.cookie = `email=${this.email}`;
            document.cookie = `pass=${this.pass}`;
            document.cookie = `full_name=${this.user.full_name}`;
            document.cookie = `phone_num=${this.user.phone_num}`;
            await this.$router.push('/userPage');
            console.log("2")
          } else {
            alert("Неверный логин и/или пароль")
          }
        } catch (e) {
          console.log(e);
          alert("Сервер недоступен, попробуйте позже");
        }
      } else
        alert("Поля email и password не могут быть пустыми");
    }
  },
  async created() {
    // При открытии смотрим куки. При наличии логина и пароля - пытаемся авторизоваться
    let email = document.cookie.replace(/(?:(?:^|.*;\s*)email=\s*\s*([^;]*).*$)|^.*$/, "$1");
    let password = document.cookie.replace(/(?:(?:^|.*;\s*)pass=\s*([^;]*).*$)|^.*$/, "$1");
    if (email !== "" && password !== "")
      await this.$router.push('/userPage');
  }
}
</script>

<style>
.auth-h2{
	margin-top: 70px;
	font-weight: 400;
	text-align: center;
	font-size: 1.5em;
	margin-bottom:20px;
}

.auth-form{
	background-color: white;
	width: 30%;
	border-radius: 10px;
	border: 0.5px solid #E5E5E5;
	margin: auto auto !important;
}

.main{
align-items: center	
}

.fields{
	padding: 35px 60px
}
.auth-field{
	width:100%; 
	margin-bottom: 25px;
      clear:both; 
      text-align:left;

}
.others{
	display: flex !important;
	align-items: center!important;
	justify-content: space-between;
	margin-bottom: 25px;
}
.auth-link{
	text-decoration: none;
	margin-left: 20px;
}

.vk{
	background-color: #2787F5;
}

.od{
	background-color: #F78306;
}

.fb{
	background-color: #4867AA;
}

.ico{
	width: 50px;
	height: 50px;
	border: none;

}
.imic{
	width: 30px;
}

.imich{
	height: 30px;
}

.social{
	display: flex;
	align-items: center;
}
</style>