<template>
  <header>
    <nav class="navbar">
      <router-link class="navbar-brand" to="/"><b class="header">Dostavka</b></router-link>

      <ul class="links justify-content-center">
        <li>
          <router-link class="nav-link" to="/" v-if="!email">Главная</router-link>
        </li>
        <li>
          <router-link class="nav-link" to="/userPage" v-if="email">Главная</router-link>
        </li>
        <li>
          <router-link class="nav-link" to="/plug">О нас</router-link>
        </li>
        <li>
          <router-link class="nav-link" to="/plug">Услуги</router-link>
        </li>
        <li>
          <router-link class="nav-link" to="/plug">Отзывы</router-link>
        </li>
        <li>
          <router-link class="nav-link" to="/orders" v-if="email">Мои заказы</router-link>
        </li>
      </ul>
      <ul class="btns" v-if="email">
        <li>
          <button class="btn btn-primary" @click="logout">Выйти</button>
        </li>
      </ul>

      <ul class="btns" v-if="!email">
        <li>
          <router-link to="/autho" type="button" class="btn btn-outline-primary">Авторизация</router-link>
        </li>
        <li>
          <router-link to="/reg" type="button" class="btn btn-outline-primary">Регистрация</router-link>
        </li>

      </ul>



      <!--<div class="dropdown">
        <button class="btn btn-secondary dropdown-toggle" type="button" id="dropdownMenu2" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
          <div class="avatar">
            <img class="avatar" src="@/assets/ava.jpg">
          </div>
          <span>Анна</span>
        </button>

        <div class="dropdown-menu" aria-labelledby="dropdownMenu2">
          <button class="dropdown-item" type="button">Профиль</button>
          <button class="dropdown-item" type="button">Настройки</button>
          <button class="dropdown-item exit" type="button">Выйти</button>
        </div>
      </div>-->
    </nav>
  </header>
</template>

<script>
export default {
  name: 'Header',
  props: {
    cookie: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      email: "",
    }
  },
  watch: {
    cookie() {
      this.setEmail()
    }
  },
  mounted() {
    this.setEmail()
  },
  methods: {
    setEmail() {
      this.email = this.cookie.replace(/(?:(?:^|.*;\s*)email=\s*\s*([^;]*).*$)|^.*$/, "$1");
    },
    async logout() {
      //"Удаляем" куки и отправляем на страницу входа
      let cookies = document.cookie.split(";");
      for (let i = 0; i < cookies.length; i++) {
        let cookie = cookies[i];
        let eqPos = cookie.indexOf("=");
        let name = eqPos > -1 ? cookie.substr(0, eqPos) : cookie;
        document.cookie = name + "=;expires=Thu, 01 Jan 1970 00:00:00 GMT";
      }
      await this.$router.push('/autho');
    }
  }
}
</script>

<style>

header{
  background-color: white;
}
